<!--  -->
<template>
  <div style="background: white; margin-top: 6px;" class="result-wrapper">

    <div v-if="isPublished">
      <!--  -->
      <div v-if="lotteryVo.lotteryType === 1 && isBanker" class="box-published-banker box1">
        <div class="item-published-banker-left">
          
          <span v-for="(option, index) in options">
            {{bets[index]}}{{optionLetters[index]}}
            <span v-if="index < options.length - 1">, </span>
          </span>
        </div>
        <div v-if="calcGainedGold >= 0" class="item-published-right-win">
          +{{calcGainedGold}}
        </div>
        <div v-else class="item-published-right-loss">
          {{calcGainedGold}}
        </div>
      </div>
      <!--  -->
      <div v-if="lotteryVo.lotteryType === 1 && !isBanker" class="box2">
        <div v-for="(option, index) in options">
          <div v-if="mineBets[index] > 0">
            <div class="box-published-user">
              <div>
                <!--icon-->
                <img v-if="option.is_right" src="../../assets/quiz_correct.png" class="result-win-icon">
                <!--icon-->
                <img v-else src="../../assets/quiz_error.png" class="result-win-icon">
              </div>
              <div class="item-published-left">
                {{optionLetters[index]}}: {{mineBets[index]}}; {{Math.abs(calcMineGainedGold(index, false, mineBetsCoin))}}
                {{Math.abs(calcMineGainedGold(index, false, mineBetsBean))}}
              </div>
              <div v-if="option.is_right" class="item-published-right-win">
                +{{calcMineGainedGold(index, true, mineBets)}}
              </div>
              <div v-else class="item-published-right-loss">
                {{calcMineGainedGold(index, false, mineBets)}}
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--  -->
      <div v-if="lotteryVo.lotteryType === 2 && isBanker" class="box-published-user flex-row box3">
        <div></div>
        <div class="item-published-right-loss">-{{parseInt(lotteryVo.coinExpendTotal)}}</div>
      </div>

      <!--  -->
      <div v-if="lotteryVo.lotteryType === 2 && !isBanker" class="box-published-user flex-row box4">
        <img src="../../assets/quiz_correct.png" class="result-win-icon">
        <div style="margin-left: 8px;"></div>
        <div class="item-published-right-win">+{{parseInt(lotteryVo.userCoins)}}</div>
      </div>
    </div>

    <!---->
    <div v-else>
      <div class="box2">
        <div v-for="(option, index) in options">
          <div v-if="mineBets[index] > 0">
            <div class="box-published-user">
              <div class="item-published-left">
                {{optionLetters[index]}}: {{mineBets[index]}}; {{Math.abs(calcMineGainedGold(index, false, mineBetsCoin))}}
                {{Math.abs(calcMineGainedGold(index, false, mineBetsBean))}}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    data () {
      return {
        // 
        optionLetters: [ 'A', 'B', 'C' ]
        // "", 
//        myGainedGold: [0, 0, 0]
      }
    },
    props: {
      // lotteryVo                             null
      // lotteryVo.lotteryType                       1  2 
      // lotteryVo.coinExpendTotal           
      // lotteryVo.userCoins    
      lotteryVo: {
        type: Object,
        required: true,
        validator: function (value) {
          return value
        }
      },
      // 
      gold: { type: Number, required: true },
      //  
      isPublished: { type: Boolean, default: false },
      // 
      isBanker: { type: Boolean, default: false },
      // :, 
//      result: Number,
      // 
      options: {
        type: Array,
        required: true,
        validator: function (value) {
          // , 
          return value.length <= 3
        }
      },
      // 
      mineBets: {
        type: Array,
        default: function () {
          return [ 0, 0, 0 ]
        },
        validator: function (value) {
          return value.length <= 3
        }
      },
      // 
      mineBetsCoin: {
        type: Array,
        default: function () {
          return [ 0, 0, 0 ]
        },
        validator: function (value) {
          return value.length <= 3
        }
      },
      // 
      mineBetsBean: {
        type: Array,
        default: function () {
          return [ 0, 0, 0 ]
        },
        validator: function (value) {
          return value.length <= 3
        }
      }
    },
    computed: {
      //  
      bets: function () {
        let sumBets = [0, 0, 0]
        for (var i = 0; i < this.options.length; i++) {
          sumBets[i] = this.options[i].sum
        }
        return sumBets
      },
      // 
      calcGainedGold: function () {
        var gained = 0
        for (var i = 0; i < this.options.length; i++) {
          if (this.options[i].is_right) {
            gained -= this.gold * this.options[i].sum * (this.options[i].odds - 1.0)
          } else {
            gained += this.gold * this.options[i].sum
          }
        }
        // 
        return Math.round(gained)
      }
    },
    methods: {
//      todo 
      // 
      calcMineGainedGold (index, win, goldType) {
        let odds = win ? (this.options[index].odds - 1.0) : 1.0
        let num = goldType[index]
        let gained = this.gold * odds * num
        if (!this.options[index].is_right) {
          gained = 0 - gained
        }
        return Math.round(gained)
      }
    },
    mounted () {
      setTimeout(() => {
        console.log('', this.isBanker, this.lotteryVo.lotteryType, this.options)
      }, 1000)
    },
    watch: {

    }
  }
</script>

<style scoped>
  .result-wrapper {
    color: #333;
  }
  .box-published-banker {
    background: white;
    display: -webkit-flex;  /* safari */
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-left: 12px;
    margin-right: 12px;
    margin-top: 4px;
    font-size: 15px;
  }
  .item-published-banker-left {
    font-size: 15px;
  }
  .box-published-user {
    background: white;
    display: -webkit-flex;  /* safari */
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-left: 12px;
    margin-right: 12px;
    margin-top: 4px;
    font-size: 14px;
  }
  .item-published-left {
    font-size: 14px;
  }
  .item-published-right-win {
    margin-left: auto;
    border: 1px #fc6868 solid;
    color: #fc6868;
    font-size: 12px;
    border-radius: 4px;
    padding: 1px 4px;
  }
  .item-published-right-loss {
    margin-left: auto;
    border: 1px #4ac741 solid;
    color: #4ac741;
    font-size: 12px;
    border-radius: 4px;
    padding: 1px 4px;
  }

  .result-win-icon {
    width: 13px;
    height: 13px;
    margin-right: 4px;
  }
</style>



