<template>
  <div class="box-buy">
    <div v-for="(option, index) in options" class="box-choose">
      <div>{{optionLetters[index]}}</div>
      <div>{{option.answer}}</div>
      <div v-if="isBanker">
        <!---->
        <img v-if="option.is_right" src="">
      </div>
      <div v-else-if="!isPublished">
        <!--{{mineBets[index]}}-->
        {{option.sum}}
      </div>
      <div v-else>
        <!---->
        <img v-if="option.is_right" src="">
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        // 
        optionLetters: [ 'A', 'B', 'C' ]
      }
    },
    props: {
      // 
      showDetail: { type: Boolean, default: false },
      //  
      isPublished: { type: Boolean, default: false },
      // 
      discuss: { type: Number, required: true },
      // 
      options: {
        type: Array,
        required: true,
        validator: function (value) {
          // , 
          return value.length <= 3
        }
      },
      // 
      mineBets: {
        type: Array,
        default: function () {
          return [ 0, 0, 0 ]
        },
        validator: function (value) {
          return value.length <= 3
        }
      },
      // 
      isBanker: { type: Boolean, default: false }
      // :, 
//      result: Number
    },
    computed: {

    }
  }
</script>

<style scoped>
  .box-buy {
    display: -webkit-flex;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    margin-left: 12px;
    margin-right: 12px;
  }

  .box-choose {
    display: -webkit-flex;
    display: flex;
    flex-flow: row nowrap;
    flex:1;
  }

  .box-choose div:nth-child(1){
    margin-right: 6px;
    color: #333;
  }

  .box-choose div:nth-child(2){
    margin-right: 8px;
    color: #333;
  }

  .box-choose:nth-child(1) div:nth-child(3){
    color: #fc6868;
    /*font-size: 10px;*/
  }

  .box-choose:nth-child(1) div:nth-child(4){
    color: #fc6868;
    /*font-size: 10px;*/
  }

  .box-choose:nth-child(2) div:nth-child(3){
    color: #6f84bb;
    /*font-size: 10px;*/
  }

  .box-choose:nth-child(2) div:nth-child(4){
    color: #6f84bb;
    /*font-size: 10px;*/
  }
</style>



