import Vue from 'vue'
import confirm from './components/confirm.vue'

let MyMsgConstructor = Vue.extend(confirm)

let instance

var MyMsg = (options = {}) => {
  // confirm
  if (document.getElementsByClassName('dm-confirm').length) {
    return
  }
  instance = new MyMsgConstructor({
    data: {
      options: options,
      title: options.title || 'title',
      mes: options.mes || 'mes',
      isChecked: typeof options.checked === 'boolean' ? options.checked : 'noChecked', // Boolean  :
      btnType: options.btnType || 'default',  // 'bet'
      btnText: options.btnText || '',  // 
      textAlign: options.textAlign || 'center'
    },
    methods: {
      confirmCb: options.confirmCb || function () {}, // 
      cancelCb: options.cancelCb || function () {}  // 
    }
  })
//  Vue  el  DOM  vm.$mount() 
  let el = instance.$mount().$el
  document.body.appendChild(el)
  return instance
}

export default MyMsg



